import React, { useState } from 'react';
import './style.scss';
import ModalComponent from '../Components/Modal';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPhone, faCommentSms } from "@fortawesome/free-solid-svg-icons";
import { useDisclosure, Collapse, Box, Button, SlideFade } from '@chakra-ui/react';

type QuestionOption = {
    label: string;
    price: number;
};

type Question = {
    question: string;
    info: string,
    options: QuestionOption[];
};

type Questions = {
    [key: string]: Question[];
};

const questions: Questions = {
    initial: [
        {
            question: '  .',
            info: '',
            options: [
                { label: '', price: 10 }, 
                { label: '', price: 13 }, 
                { label: '1.5', price: 13 }, 
                { label: '', price: 7 }, 
                { label: '', price: 7 }
            ],
        },
    ],
    size: [
        {
            question: '  .',
            info: '',
            options: [
                { label: '5 ', price: 4 }, 
                { label: '7 ', price: 5 }, 
                { label: '9 ', price: 7 }, 
                { label: '12 ', price: 10 }, 
                { label: '12 ', price: 10 }
            ],
        },
    ],
    bathroom: [
        {
            question: '   ?',
            info: '',
            options: [
                { label: '1', price: 0 }, 
                { label: '2', price: 7 }, 
                { label: '3 ', price: 14 }, 
            ],
        },
    ],
    more: [
        {
            question: '  ?',
            info: ', ,     .',
            options: [
                { label: '', price: 0 }, 
                { label: '', price: 2 }, 
                { label: '', price: 3 }, 
                { label: '', price: 2 }, 
                { label: '+', price: 5 }, 
                { label: '+', price: 4 }, 
                { label: '+', price: 4 }, 
                { label: '++', price: 7 }, 
            ],
        },
    ],
    add: [
        {
            question: '   ?',
            info: '',
            options: [
                { label: '', price: 0 }, 
                { label: ' ', price: 3 }, 
                { label: ' ', price: 3 }, 
                { label: '  ', price: 5 }, 
            ],
        },
    ],
    result: [ //   
        {
            question: ' ',
            info: '',
            options: [
                { label: ' .', price: 0 }
            ],
        },
    ],
};

export const Estimate: React.FC = () => {
    const [totalEstimate, setTotalEstimate] = useState<number>(0); 
    const [responses, setResponses] = useState<QuestionOption[]>([]);
    const [currentStage, setCurrentStage] = useState<'initial' | 'size' | 'bathroom' | 'more' | 'add' | 'result'>('initial');
    const [isCallModalOpen, setCallModalOpen] = useState(false);
    const [isSmsModalOpen, setSmsModalOpen] = useState(false);
    const [isQuestionVisible, setQuestionVisible] = useState(true);
    const [moreMsg, setMoreMsg] = useState<string>(''); 

    const phoneNumber = '+821024881056';
    const message = ',  .';

    const handleCall = () => {
        window.location.href = `tel:${phoneNumber}`;
    };

    const handleSms = () => {
        window.location.href = `sms:${phoneNumber}?body=${encodeURIComponent(message)}`;
    };

    const handleConfirmCall = () => {
        handleCall();
        setCallModalOpen(false);
    };

    const handleConfirmSms = () => {
        handleSms();
        setSmsModalOpen(false);
    };

    const handleOptionSelect = (option: QuestionOption) => {
        const newResponses = [...responses, option];
        setResponses(newResponses);

        setQuestionVisible(false); //  

        setTimeout(() => {
            if (currentStage === 'initial') {
                if (option.label === '') {
                    setCurrentStage('more');
                    setMoreMsg('')
                } else if (option.label === '') {
                    setCurrentStage('bathroom');
                    
                } else {
                    setCurrentStage('size');
                }
            } else if (currentStage === 'size') {
                setCurrentStage('add');
                if (option.label === '12 ') {
                    setMoreMsg('')
                } else {
                    setMoreMsg('')
                }
            } else if (currentStage === 'bathroom') {
                if (option.label === '3 ') {
                    setMoreMsg('')
                } else {
                    setMoreMsg('')
                }
                setCurrentStage('more');
            } else if (currentStage === 'add') {
                setCurrentStage('more');
            } else if (currentStage === 'more') {
                calculateEstimate(newResponses);
                setCurrentStage('result');
            }
            setQuestionVisible(true); //   
        }, 300); //    
    };

    const calculateEstimate = (responses: QuestionOption[]) => {
        const estimate = responses.reduce((total, response) => total + response.price, 0);
        setTotalEstimate(estimate);
    };

    const handleRestart = () => {
        setResponses([]);
        setCurrentStage('initial');
        setTotalEstimate(0);
        setQuestionVisible(true); //    
    };

    const currentQuestions = questions[currentStage]; //     

    return (
        <div className='estimate'>
            <div className='tit'>
                <span className='main_tit'>
                    <p>!</p>
                    <p>   .</p>
                </span>
                <p className='body_tit'>      .<br/>
                        .
                </p>
            </div>
            <div className='box'>
                {currentStage !== 'result' ? (
                    <div>
                        <SlideFade in={isQuestionVisible} offsetY='20px'>
                            <Box>
                                <div className='quast_box'>
                                    <h2>{currentQuestions[0].question}</h2>
                                </div>
                                <div className='quast_info'>{currentQuestions[0].info}</div>
                                <div className='answer_box'>
                                    {currentQuestions[0].options.map((option: QuestionOption, index: number) => (
                                        <button key={index} onClick={() => handleOptionSelect(option)}>
                                            {option.label}
                                        </button>
                                    ))}
                                </div>
                            </Box>
                        </SlideFade>
                    </div>
                ) : (
                    <div className='result'>
                        <p className='total'> <b>{totalEstimate}</b>
                        {moreMsg && <span>{moreMsg}</span>} .</p>
                        <p className='total_info'>12 ,  3 ,    <br/>
                                 .</p>
                        <button className='restart_btn' onClick={handleRestart}></button>
                        <div className='btn_set'>
                            <button className='call_btn' onClick={() => setCallModalOpen(true)}><FontAwesomeIcon icon={faPhone}  />  </button>
                            <button className='sms_btn' onClick={() => setSmsModalOpen(true)}><FontAwesomeIcon icon={faCommentSms}  />  </button>
                        </div>
                        <ModalComponent msg=" ?"
                            isOpen={isCallModalOpen} 
                            onClose={() => setCallModalOpen(false)}                         
                            onConfirm={handleConfirmCall}  />
                        <ModalComponent 
                            msg=" ?" 
                            isOpen={isSmsModalOpen} 
                            onClose={() => setSmsModalOpen(false)}             
                            onConfirm={handleConfirmSms} 
                        />
                    </div>
                )}
            </div>
        </div>
    );
};
